/* Footer Styling */
.footer {
  background: #004d99;
  color: white;
  text-align: center;
  padding: 30px 20px;
}

.footerContainer {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Logo */
.footerLogo {
  height: 50px;
  width: auto;
  margin-bottom: 15px;
}

/* Navigation Links */
.footerNav {
  display: flex;
  gap: 20px;
  margin-bottom: 15px;
}

.footerLink {
  color: white;
  text-decoration: none;
  font-size: 16px;
  transition: color 0.3s ease;
}

.footerLink:hover {
  color: #ff9800;
}

/* Contact Links */
.contactLinks {
  display: flex;
  gap: 15px;
  margin-bottom: 15px;
}

.contactLink {
  color: white;
  text-decoration: none;
  font-size: 16px;
  display: flex;
  align-items: center;
  gap: 5px;
  transition: color 0.3s ease;
}

.contactLink:hover {
  color: #ff9800;
}

/* Icons */
.icon {
  font-size: 18px;
}

/* Copyright */
.copyright {
  font-size: 14px;
  opacity: 0.8;
}
